// global rules
body {
  color: $sub2Color;
}

.h1, h2, h3 {
  font-family: $headings;
  position:relative;
  i {
    position: absolute;
    font-size:12px;
    margin:0 -18px;
    top:50%;
    transform: translateY(-50%);
    &:nth-of-type(2) {
      margin-left:12px;
    }
  }
}

a, a:hover, a:visited {
  color:inherit;
  text-decoration: none;
}

// global classes
.wrapper {
  max-width:1500px;
  margin:auto;
  position:relative;
}

.divider {
  position:absolute;
  bottom:-23px;
  left:0;
  width:100%;
  height:23px;
  z-index:1;
  &.divider__yellow {
    background:url('../imgs/wave-border-yellow.png') center repeat-x;
  }
  &.divider__green {
    background:url('../imgs/wave-border-green.png') center repeat-x;
  }
  &.divider__red {
    background:url('../imgs/wave-border-red.png') center repeat-x;
  }
  &.animated {
    animation:wave 3s ease 1 forwards;
  }
}

.opining.left, .opining.right {
  position: fixed;
  top:0;
  width:50%;
  height:calc(100vh - 20px);
  background:$mainColor url('../imgs/bg-pattern.png');
  opacity:.95;
  z-index:999999;
  transition:all 3s ease;
  padding-left:10px;
  &.active {
    width:0;
    height:calc(100vh - 10px);
  }
}
.opining.left {
  left:0;
  .divider {
    transform: rotateY(-180deg);
  }
}
.opining.right {
  right:0;
}

// font faces
@font-face {
  font-family: 'almost-japanese-smooth';
  src: url('fonts/AlmostJapaneseSmooth.ttf') format('truetype');
}

@font-face {
  font-family: 'sanka';
  src: url('fonts/SANKA___.TTF') format('truetype');
}
@font-face {
  font-family: 'logo';
  src: url('fonts/Geovana.otf') format('opentype');
}

// animations
@keyframes wave {
  0% {background-position-x: 0;}
  100% {background-position-x: 100%;}
}