$social:(
  facebook:  (color: #3b5998),
  twitter:   (color: #55acee),
  instagram: (color: #e95950),
  tumblr:    (color: #35465D),
  vine:      (color: #00bf8f)
);

.footer {
  background:url('../imgs/5.png') bottom left no-repeat;
  background-size:contain;
  .txt-layout{
    background-image:url('../imgs/bg-pattern.png');
    border-right:1px dashed #{$lightGreen};
  }
  .txt {
    padding-bottom:30px;
    margin:0px auto;
    border-right:1px dashed #{$lightGreen};
    position: relative;
    top:50%;
    transform: translateY(-50%);
    margin-top:10px;
    .h1 {
      @include responsiveFont (50,80,300,1550);
      color:$sub2Color;
      font-family: $mainHeading;
      text-shadow:0 0 20px #fff;
      border-bottom:1px dashed #{$lightGreen};
      margin:0;
    }
    .right,.sign {
      font-size:14px;
      padding:15px 0;
      display:block;
      width:90%;
      border-bottom:1px dashed #{$lightGreen};
      border-right:1px dashed #{$lightGreen};
      background-image:url('../imgs/bg-pattern.png');
    }
    .sign {
      width:80%;
      span {
        font-family: 'logo';
        font-size:18px
      }
    }
  }
  .social {
    padding-bottom:30px;
    a {
      display:block;
      margin:0 10px;
      width:45px;
      height:45px;
      line-height: 40px;
      border-radius:50%;
      font-size:25px;
      border:1px dashed transparent;
      transition:all .5s ease;
      @each $item,$x in $social {
        &.#{$item} {
          background-image:url('../imgs/bg-pattern.png');
          border-color:$lightGreen;
          &:hover {
            background:#{map-get($x, color)} url('../imgs/bg-pattern.png');
            color:$subColor;
            border-color: transparent;
          }
        }
      }
    }
  }
}

@media (max-width:991px) {
  .footer {
    background-size:300px;
  }
}
@media (max-width:767px) {
  .footer {
    background:none;
  }
}