.nav {
  position:fixed;
  width:450px;
  top:0;
  left:50%;
  transform: translateX(-50%) translateY(-100%);
  z-index:99999;
  transition:all .3s linear;
  &.active {
    transform: translateX(-50%) translateY(0%);
  }
  ul {
    border-radius:0 0 20px 20px;
    border:1px solid #{$lightGreen};
    background:$subColor url('../imgs/bg-pattern.png');
    border-top:none;
    width:100%;
    height:45px;
    line-height: 30px;
    display:flex;
    justify-content:space-between;
    overflow:hidden;
    li {
      color:$mainColor;
      flex-grow:1;
      transition:all .5s ease;
      &:not(:last-of-type) {
        border-right:1px solid #{$lightGreen};
      }
      &:hover {
        background:$sub2Color url('../imgs/bg-pattern.png');
        color:$subColor
      }
      a {
        width:100%;
        height:100%;
        display: inline-block;
      }
    }
  }
}

.nav-toggle-btn {
  position: fixed;
  top:20px;
  right:0;
  z-index:99999;
  width:100%;
  .toggle-btn {
    position: absolute;
    top:0;
    right:35px;
    border:1px solid #{$lightGreen};
    height:53px;
    padding:20px 0;
    width:70px;
    background:$subColor url('../imgs/bg-pattern.png');
    cursor: pointer;
    &.active {
      padding:25px 0;
      span:nth-of-type(1) {
        transform: rotate(45deg);
      }
      span:nth-of-type(2) {
        display:none;
      }
      span:nth-of-type(3) {
        transform: rotate(-45deg);
      }
    }
  }
  span {
    height:1px;
    width:30px;
    background-color:$mainColor;
    display:block;
    margin:auto;
    transition:all .5s ease;
    &:nth-of-type(2) {
      margin:5px auto;
    }
  }
}

@media (max-width:767px) {
  .nav {
    width:150px;
    left:0;
    transform: translateX(-100%);
    &.active {
      transform:translateX(0%);
    }
    ul {
      display:block;
      height:100%;
      li:not(:last-of-type) {
        border-bottom:1px dashed #{$lightGreen};
        border-right:none;
      }
      a {
        padding:10px 0;
      }
    }
  }
}