// header 
.home .header {
    background:#{$subColor} url('../imgs/4.png') top left no-repeat;
    background-size:contain;
    height:calc(100vh - 13px);
    max-height:700px;
    position:relative;
    .container-fluid, .row {
      height:100%;
    }
    .txt {
      position:relative;
      top:50%;
      transform:translateY(-50%);
    }
    h1 {
      @include responsiveFont (50,180,300,1500);
      color:$sub2Color;
      font-family: $mainHeading;
    }
    .inner {
      max-width:700px;
    }
    p {
      max-width:500px;
      padding-left:30px;
    }
    a {
      width:180px;
      height:50px;
      line-height: 50px;
      border-radius:50px;
      background-color:$mainColor;
      position:relative;
      right:calc(-100% + 200px);
      color:$subColor;
      transition:all .5s ease;
      overflow: hidden;
      &:before {
        content:'';
        position: absolute;
        top:100%;
        left:0;
        width:100%;
        height:calc(100% + 15px);
        background:url('../imgs/btn-bg.png') left top repeat-x;
        transition:all 2s ease;
      }
      &:hover:before {
        top:-13px;
        background-position-x: 100%;
      }
      span {
        position:relative;
        z-index:1;
      }
    }
}

// about
.home .about{
  background-color:$sub2Color;
  padding:60px 0 50px 0;
  position:relative;
  color:$subColor;
  .txt {
    position:relative;
    top:50%;
    transform: translateY(-50%);
  }
  .h1 {
    @include responsiveFont (30,60,300,1500);
    margin-top:-7px;
  }
  p {
    max-width:500px;
  }
}

// special
.home .special {
  padding:60px 0 50px 0;
  background-color:$subColor;
  background:$subColor url('../imgs/16.png') left no-repeat;
  background-size:contain;
  position:relative;
  .h1 {
    @include responsiveFont (30,60,300,1500);
  }
  .img {
    max-width:500px;
    position:relative;
    top:50%;
    transform: translateY(-50%);
  }
  .txt {
    max-width: 350px;
    height: 400px;
    margin:auto;
    padding:35px 15px;
    border: 50px solid transparent;
    border-image: url("../imgs/torii-blossoms-red.png") 300 stretch;
    h4,p,.price {background:$subColor url('../imgs/bg-pattern.png');}
    h4 {
      color:$mainColor;
    }
    p {
      line-height: 2.5;
      font-weight: bold;
    }
    .price {
      color:$mainColor;
    }
  }
}

// menu
.home .menu {
  padding:60px 0 50px 0;
  background-color:$mainColor;
  position:relative;
  .container-fluid {
    border:1px dashed #{$sub2Color};
    height:100%;
    .h1 {
      font-size:45px;
    }
  }
  .heading {
    height:100%;
    max-height:780px;
    background:$subColor url('../imgs/bg-pattern.png');
    padding:30px;
    position: relative;
    &:before {
      content:'';
      position: absolute;
      top:30px;
      left:0;
      width:100%;
      height:100%;
      background:url('../imgs/1.png') bottom no-repeat;
    }
    .h1 {
      font-size:80px;
      color:$mainColor;
    }
  }
  .menu-box {
    height:100%;
    background:$subColor url('../imgs/bg-pattern.png');
    padding:30px;
    .h1 {
      margin-left:30px;
    }
    .dishes-layout {
      display:flex;
      padding-bottom:30px;
      .box {
        flex-grow:1;
        .dish {
          margin-top:30px;
          border-right:1px dashed #{$sub2Color};
          border-bottom:1px dashed #{$sub2Color};
          cursor:pointer;
          .img {
            max-width:100px;
            transition: all .5s ease;
          }
          &:hover .img {
            transform:rotate(45deg);
          }
          p {
            max-width:300px;
          }
          .price {
            font-size:20px;
            color:$mainColor;
            padding-right:15px;
            margin-top:15px
          }
        }
        &:nth-of-type(2) {
          .dish {
            border-left:1px dashed #{$sub2Color};
            border-right:none;
          }
        }
      }
    }
    .rose {
      height:30px;
      img {
        max-height: 100%;
      }
    }
  }
}

// contact
.home .contact {
  position:relative;
  background:#{$subColor} url('../imgs/28.png') bottom right no-repeat;
  background-size:contain;
  padding:60px 30px;
  .h1 {
    @include responsiveFont (35,60,300,1500);
    color:$sub2Color;
  }
  form {
    max-width:300px;
    margin:auto;
    input,textarea {
      width:100%;
      background:$sub2Color url('../imgs/bg-pattern.png');
      border:none;
      margin-top:15px;
      color:$subColor;
      height:50px;
      padding:0 15px;
      transition:all .5s ease;
      border:1px solid #{$sub2Color};
      outline: none;
      &::placeholder {
        color:$subColor
      }
    }
    input:focus,textarea:focus {
      background-color:rgba($color: $sub2Color, $alpha: .1);
      border-color:rgba($color: $sub2Color, $alpha: .1);
      color:$sub2Color;
      &::placeholder {
        color:$sub2Color
      }
    }
    textarea {
      resize: none;
      height:200px;
      border-radius:0 0 50% 50%;
      padding-top:15px;
      overflow: hidden;
    }
    input[type='submit'] {
      cursor:pointer;
    }
  }
  .info {
    background:#{$subColor} url('../imgs/bg-pattern.png');
    position: relative;
    max-width:300px;
    margin:0px auto 60px auto;
    top:50%;
    transform: translateY(-50%);
    padding:15px;
    border:1px solid #{$lightGreen};
    .container {
      border:1px dashed #{$lightGreen};
      padding:15px;
    }
  }
}

/************ responsive ************/
@media (min-width:1500px) {
  .home .header h1 {
    font-size:200px;
  }
  .home .about .h1,.home .special .h1, .home .contact .h1 {
    font-size:60px;
  }
}

@media (max-width:991px) {
  // about
  .home .about p {
    max-width:100%;
  }
  // special
  .home .special .img {
    max-width:300px;
    padding-top:30px;
  }
  // menu
  .home .menu .heading:before {
    background-size: 120px;
    background-position: top left;
  }
  // contact
  .home .contact {
    background:#{$subColor} url('../imgs/29.png') bottom right no-repeat;
    background-size:auto;
  }
}
@media (max-width:767px) {
  // header
  .home .header {
    height:auto;
    padding-top:250px;
    .txt {
      background:#{$subColor} url('../imgs/bg-pattern.png');
      padding:30px 0;
      text-align:center;
      p {
        margin:15px auto;
        padding:0;
      }
      a {
        right:0;
      }
    }
  }
  // special
  .home .special .img {
    padding-top:0px;
  }
  // menu
  .home .menu .menu-box .dishes-layout {
    display:block;
    font-size:14px;
  }
}
@media (max-width:400px) {
  .home .contact .info {
    font-size:14px;
    word-wrap: break-word;
  }
}